import React from 'react';
import './Statistics.css';
import CountUp from 'react-countup';


function Statistics() {
  return (
    <div className='container'>
      <div className='statistics' id='statistics'>
        <ul className='statistics__list'>
          <li className='statistics__item'>
            <span className='statistics__caption'></span>
            <CountUp className='statistics__title font__family_macherie' start={0} end={80000} separator=' ' />
            <p className='statistics__subtitle'> <span className='font__weight_bold'></span>   &nbsp;2022&nbsp;</p>
          </li>
          <li className='statistics__item'>
            <span className='statistics__caption'></span>
            <CountUp className='statistics__title font__family_macherie' start={0} end={80000} separator=' ' />
            <p className='statistics__subtitle'> <span className='font__weight_bold'></span>  &nbsp;2023&nbsp;</p>
          </li>
          <li className='statistics__item'>
            <span className='statistics__caption'></span>
            <CountUp className='statistics__title font__family_macherie' start={0} end={385000} separator=' ' />
            <p className='statistics__subtitle'> <span className='font__weight_bold'></span>  &nbsp;2025&nbsp;</p>
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Statistics;
